import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PickListNewService } from 'src/app/service/pick-list-new.service';
import { DataService } from 'src/app/service/data.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-pick-list-view-product',
  templateUrl: './pick-list-view-product.component.html',
})
export class PickListViewProductComponent implements OnInit {
  viewId: any;
  objectArray: any;
  date_format: string = "M/d/yy";
  timezone: string
  time_format: string;
  currentCompany: any;

  constructor(
    private activatedRoute: ActivatedRoute,
    private dataService: DataService,
    private location: Location,
    private pickListNewService: PickListNewService
  ) {

    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
    this.dataService.currentCompany.subscribe((response) => {
      this.currentCompany = response;
    });
    this.dataService.currentCompany.subscribe((response) => {
      if (response) {
        this.timezone = String(response.timezone.format);
        this.time_format = response.time_format.label;
        this.date_format = response.date_format.label;
      }
    });
  }
  ngOnInit(): void {
    this.pickListNewService.getPickListNewviewProductById(this.viewId).subscribe((response) => {
      if (response.success) {
        this.objectArray = response.data;
      }
    });
  }

  print() {
    window.print();
  }
  back() {
    this.location.back();
  }

}

